<template>
  <div class="">
    <q-stepper
      ref="stepper"
      alternative-labels
      color="primary"
      animated
      class=""
    >
      <q-step
        v-for="(level, index) in stageLevel"
        :key="level.id"
        :name="index"
        :title="level.label"
        :prefix="index + 1"
        :done="currentStep >= index + 1"
        :done-color="doneColor"
      >
      </q-step>
    </q-stepper>
  </div>
</template>
<script>
export default {
  name: "Stepper",

  props: {
    stageLevel: {
      type: Array,
      default: () => [],
    },

    currentStep: {
      type: Number,
      default: 0,
    },

    doneColor: {
      type: String,
      default: "",
    },
  },
};
</script>
